import React, { useEffect } from "react";
import { FaExclamationTriangle, FaTimes } from "react-icons/fa";
import "./ConfirmationModal.css";

const ConfirmationModal = ({
  isOpen,
  onConfirm,
  onCancel,
  title = "Confirm Action",
  message = "Are you sure you want to proceed?",
  confirmText = "Yes",
  cancelText = "No",
  type = "warning", // warning, danger, info
}) => {
  useEffect(() => {
    if (isOpen) {
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "unset";
    }

    return () => {
      document.body.style.overflow = "unset";
    };
  }, [isOpen]);

  // Handle escape key
  useEffect(() => {
    const handleEscape = (e) => {
      if (e.key === "Escape" && isOpen) {
        onCancel();
      }
    };

    document.addEventListener("keydown", handleEscape);
    return () => document.removeEventListener("keydown", handleEscape);
  }, [isOpen, onCancel]);

  if (!isOpen) return null;

  const getModalConfig = () => {
    switch (type) {
      case "danger":
        return {
          iconColor: "#ef4444",
          confirmButtonClass: "confirmation-modal__button--danger",
          headerClass: "confirmation-modal__header--danger",
        };
      case "info":
        return {
          iconColor: "#3b82f6",
          confirmButtonClass: "confirmation-modal__button--info",
          headerClass: "confirmation-modal__header--info",
        };
      default: // warning
        return {
          iconColor: "#f59e0b",
          confirmButtonClass: "confirmation-modal__button--warning",
          headerClass: "confirmation-modal__header--warning",
        };
    }
  };

  const config = getModalConfig();

  return (
    <div className="confirmation-modal-overlay" onClick={onCancel}>
      <div
        className="confirmation-modal"
        onClick={(e) => e.stopPropagation()}
        role="dialog"
        aria-modal="true"
        aria-labelledby="modal-title"
        aria-describedby="modal-message"
      >
        <div className={`confirmation-modal__header ${config.headerClass}`}>
          <div
            className="confirmation-modal__icon"
            style={{ color: config.iconColor }}
          >
            <FaExclamationTriangle />
          </div>
          <button
            className="confirmation-modal__close"
            onClick={onCancel}
            aria-label="Close modal"
          >
            <FaTimes />
          </button>
        </div>

        <div className="confirmation-modal__body">
          <h3 id="modal-title" className="confirmation-modal__title">
            {title}
          </h3>
          <p id="modal-message" className="confirmation-modal__message">
            {message}
          </p>
        </div>

        <div className="confirmation-modal__footer">
          <button
            className="confirmation-modal__button confirmation-modal__button--cancel"
            onClick={onCancel}
            autoFocus
          >
            {cancelText}
          </button>
          <button
            className={`confirmation-modal__button ${config.confirmButtonClass}`}
            onClick={onConfirm}
          >
            {confirmText}
          </button>
        </div>
      </div>
    </div>
  );
};

export default ConfirmationModal;
